<template>
  <div class="box">
    <div class="head">
      <div class="center">
        <img :src="headimg"/>
        <a>{{name}}</a>
      </div>
      <img @click="friendslist" class="friends" src="../../assets/images/activity/51wawa6.png" />
    </div>

    <div class="operation">
      <div class="things" id="things">
        <div class="clawsbody" id="clawsbody">
          <img class="clawstop" src="../../assets/images/activity/51wawa14.png"/>
          <img id="clawscenter" class="clawscenter" src="../../assets/images/activity/51wawa15.png"/>
          <img class="clawsend" :src="loosen"/>
          <img v-show="giftboxShow" class="giftbox" src="../../assets/images/activity/51wawa18.png"/>
        </div>

        <img v-for="(i,index) in imgList" :class="i.gift" :src="i.img_url"/>
      </div>
    </div>
    <div class="foot">
      <div class="remote" id="remote">
        <div class="ball" id="ball" @touchmove="touchMove" @touchend="touchEnd"></div>
      </div>
      <div class="catch" @touchstart="touchDown" @touchend="touchUp">
        <img v-show="upShow" src="../../assets/images/activity/51wawa9.png"/>
        <img v-show="downShow" src="../../assets/images/activity/51wawa10.png"/>
      </div>
    </div>


    <!--提示框-->
    <div class="modal" v-show="modalShow">
      <div class="modalbody">
        <div class="delete">
          <img @click="del" src="../../assets/images/activity/51wawa41.png"/>
        </div>
        <p>{{info}}</p>
      </div>
    </div>
    <div class="resultmodal" v-show="resultShow">
      <div class="modalbody">
        <div class="delete">
          <img @click="del" src="../../assets/images/activity/51wawa41.png"/>
        </div>
        <img class="slide" src="https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa33.png"/>
        <p>{{resultinfo}}</p>
      </div>
    </div>
  </div>
</template>
<script>
  var urlhost = process.env.API_AJAX;
  import loosen from "../../assets/images/activity/51wawa16.png"
  import tighten from "../../assets/images/activity/51wawa166.png"
  import defalut from '@/assets/images/home/defalut.png'
  function getCookie(name) {
    var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    return v ? v[2] : null;
  }
  function setCookie(name, value, days) {
    var d = new Date;
    d.setTime(d.getTime() + 60*60*1000*days);
    window.document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
  }
  var g_dir = -1;
  export default {
    name:"OtherHome",
    data() {
      return {
        loosen:loosen,
        tighten:tighten,
        defalut:defalut,
        name:"",
        headimg:"",
        number:0,
        giftboxShow:false,
        upShow:true,
        downShow:false,
        num:0,
        timer:'',
        info:"",
        resultinfo:"",
        shareShow:false,
        modalShow:false,
        resultShow:false,
        down:true,
        imgList:[
          {"gift":"gfts1","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa17.png" ,"type":3},
          {"gift":"gfts2","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa18.png","type":3},
          {"gift":"gfts3","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/yellowdia3.png","type":1},
          {"gift":"gfts4","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/yellowdia.png","type":1},
          {"gift":"gfts12","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa28.png","type":2},
          {"gift":"gfts13","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa29.png","type":2},
          {"gift":"gfts15","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/bluedia2.png","type":1},
          {"gift":"gfts16","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/yellowdia3.png","type":1},
          {"gift":"gfts17","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/bluedia2.png","type":1},
          {"gift":"gfts18","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa17.png" ,"type":3},
          {"gift":"gfts19","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa18.png","type":3},
          {"gift":"gfts20","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa18.png","type":3},
        ],
        dollList:[
          {"gift":"gfts1","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa17.png" ,"type":3},
          {"gift":"gfts2","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa18.png","type":3},
          {"gift":"gfts3","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa19.png","type":1},
          {"gift":"gfts4","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa20.png","type":1},
          {"gift":"gfts5","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa21.png","type":2},
          {"gift":"gfts6","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa22.png","type":2},
          {"gift":"gfts7","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa23.png","type":2},
          {"gift":"gfts8","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa24.png","type":2},
          {"gift":"gfts9","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa25.png","type":2},
          {"gift":"gfts10","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa26.png","type":2},
          {"gift":"gfts11","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa27.png","type":2},
          {"gift":"gfts12","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa28.png","type":2},
          {"gift":"gfts13","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa29.png","type":2},
          {"gift":"gfts14","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa30.png","type":2},
          {"gift":"gfts15","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa20.png","type":1},
        ],
        otherID:""
      }
    },
    components: {
    },

    mounted:function () {
      var _this = this;
      $.ajax({
        type: 'POST',
        url: urlhost+"active/initprops",
        data$Type: "jsonp",
        data: {
          "user_id":_this.$route.query.id
        },
        success: function (response) {
            console.log(JSON.parse(response))
          var status = JSON.parse(response).status;
          if(status == 0){
            return
          }else{
            var diamonds = [];
            var gifts = [];
            var dolls = [];
            var dataArray = JSON.parse(response).message;
            dataArray.forEach(function (i) {
              switch (i.prop_type){
                case 1:
                  _this.dollList.forEach(function (j) {   //总图片
                    if(j.type == 1){
                      diamonds.push(j)
                    }
                  })
                  break;
                case 2:
                  _this.dollList.forEach(function (k) {   //总图片
                    if(k.type == 2){
                      dolls.push(k)
                    }
                  })
                  break;
                case 3:
                  _this.dollList.forEach(function (l) {   //总图片
                    if(l.type == 3){
                      gifts.push(l)
                    }
                  })
                  break;
              }
            })
            if(dolls.length >0 ){
              var g = Math.floor((Math.random()*dolls.length));
              _this.imgList.push(dolls[g])
            }
            if(gifts.length >0 ){
              var a = Math.floor((Math.random()*gifts.length));
              _this.imgList.push(gifts[a])
            }
            if(diamonds.length >0 ){
              var d = Math.floor((Math.random()*diamonds.length));
              _this.imgList.push(diamonds[d])
            }

          }

        },
      });

console.log(_this.$route.query.id)
      $.ajax({
        type: 'POST',
        url: urlhost + "active/sendinfo",
        data$Type: "jsonp",
        data: {
          "you_id": _this.$route.query.id,
        },
        success: function (response) {
          var data = JSON.parse(response).message;
          console.log(data)
          _this.name = data.nickname;
          if (data.img_url == null || data.img_url == "") {
            _this.headimg = _this.defalut;
          } else {
            _this.headimg = data.img_url;
          }
        },
      });
    },
    methods:{
      friendslist:function () {
        var _this = this;
        _this.$router.push({path:"/activity/friends"})
      },
      del:function () {
        this.modalShow = false;
        this.resultShow = false;
      },
      touchDown:function () {
        var _this = this;
        _this.resultfun("active/getnum",_this.$route.query.id);
      },
      touchUp:function () {
        var _this = this;
        _this.upShow = true;
        _this.downShow = false;
      },
      resultfun:function (urlsearch,youid) {
        var _this = this;
        var userid = getCookie("userId");
        if(_this.down == true){
          _this.upShow = false;
          _this.downShow = true;
          $.ajax({
            type: 'POST',
            url: urlhost+urlsearch,
            data$Type: "jsonp",
            data: {
              "user_id":userid,
              "you_id":youid
            },
            success: function (response) {
              var data = JSON.parse(response);
              console.log(data)
              if(data.status == 0){
                _this.info = data.message;
                _this.modalShow = true;
              }else{

                setCookie("num",data.message.snum)
                _this.number = data.message.snum;
                $("#clawscenter").animate({"height":"200px"},3000,function () {
                  $(".clawsend").attr("src",tighten);
                  _this.giftboxShow = true;
                  $("#clawscenter").animate({"height":"10px"},3000,function () {
                    $(".clawsend").attr("src",loosen);
                    _this.giftboxShow = false;
                    _this.down = true;
                    _this.resultShow = true;
                    _this.resultinfo = data.message.prop
                  })
                })
              }
            },
          });

        }else{
          return
        }
      },
      touchMove:function () {
        var _this = this;
        event.preventDefault();
        var l_dir=-1;
        var box = document.getElementById('remote');
        var touchDiv = document.getElementById('ball');
        var touch = event.targetTouches[0];
        var x = (touch.pageX-box.offsetLeft) - touchDiv.offsetWidth/2;
        var cx = document.getElementById("clawsbody").offsetLeft;
        var wx = document.getElementById("clawsbody").offsetWidth;
        var tx = document.getElementById("things").offsetWidth;
        if( x < 0 ){
          x=0;
          l_dir=2;
          if(g_dir !=l_dir){
            clearInterval(_this.timer);
            g_dir = l_dir;
            _this.timer = setInterval(function () {
              _this.num--;
              if(_this.num <= 0){
                _this.num = 0;
                document.getElementById("clawsbody").style.left = _this.num +"px"
              }else{
                document.getElementById("clawsbody").style.left = _this.num +"px"
              }

            },30)
          }
        }else if(x>box.offsetWidth-touchDiv.offsetWidth){
          x=box.offsetWidth-touchDiv.offsetWidth;
          l_dir=0;
          if(g_dir !=l_dir){
            clearInterval(_this.timer)
            g_dir = l_dir;
            _this.timer = setInterval(function () {
              _this.num ++;
              if(_this.num >= tx - wx){
                _this.num = tx - wx;
                document.getElementById("clawsbody").style.left = _this.num +"px"
              }else{
                document.getElementById("clawsbody").style.left = _this.num +"px"
              }
            },30)

          }


        }

        touchDiv.style.left = x+'px';
      },
      touchEnd:function () {
        var _this = this;
        var box = document.getElementById('remote');
        var touchDiv = document.getElementById('ball');
        touchDiv.style.left = box.offsetWidth / 2 - touchDiv.offsetWidth / 2 + 'px';
        clearInterval(_this.timer)
        g_dir=-1;
        console.log("songkai ")
      },

    }
  }
</script>

